import {
  CssBaseline,
  Box,
  Container,
  AppBar,
  Toolbar,
  Typography,
  TextField,
  Grid,
  Button
} from "@mui/material";
import { useState } from "react";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip } from "recharts";
import { styled } from "@mui/material/styles";

const data = [
  {
    name: "Neu",
    prob: 0.9
  },
  {
    name: "Pos",
    prob: 0.1
  },
  {
    name: "Neg",
    prob: 0.0
  },
  {
    name: "Q",
    prob: 0.0
  }
];

const ValidationTextField = styled(TextField)({
  "& input:valid + fieldset": {
    borderColor: "gray",
    borderWidth: 2
  },
  "& input:invalid + fieldset": {
    borderColor: "red",
    borderWidth: 2
  },
  "& input:valid:focus + fieldset": {
    borderLeftWidth: 6,
    padding: "4px !important" // override inline-style
  }
});

const ResponsiveAppBar = () => {
  return (
    <AppBar position="static" sx={{}}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ display: "flex" }}
          >
            This is appbar
          </Typography>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

const DisplaySentiment = (props: { emoji: string; sentiment: string }) => {
  const { emoji, sentiment } = props;
  return (
    <Box sx={{ justContent: "center", textAlign: "center" }}>
      <Typography sx={{ fontSize: "4rem" }}>{emoji}</Typography>
      <Typography sx={{ fontSize: "2rem" }}>{sentiment}</Typography>
    </Box>
  );
};

function App() {
  const [text, setText] = useState("");
  const [sentiment, setSentiment] = useState("");

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setText(event.target.value);
  };

  const handleSubmit = async () => {
    if (text.length === 0) {
      return;
    }
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ text: text })
    };
    const response = await fetch(
      "https://nlp-api.ppirch.me/predict",
      requestOptions
    );
    const data = await response.json();
    setSentiment(data.sentiment);
  };

  return (
    <>
      <CssBaseline />
      <Box sx={{ minHeight: "100vh" }}>
        <ResponsiveAppBar />
        <Container maxWidth="xl">
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6}>
              <Typography variant="h3" gutterBottom component="div">
                Text Sentiment Analysis
              </Typography>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Typography variant="h3" gutterBottom component="div">
                Result
              </Typography>
            </Grid>
            <Grid item xs={12} sm={6}>
              <ValidationTextField
                label="Text input"
                variant="outlined"
                defaultValue=""
                id="validation-outlined-input"
                fullWidth
                multiline
                rows={10}
                onChange={handleChange}
                required
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <BarChart
                width={500}
                height={300}
                data={data}
                margin={{
                  top: 5,
                  right: 30,
                  left: 20,
                  bottom: 5
                }}
              >
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" />
                <YAxis />
                <Tooltip />
                <Bar dataKey="prob" fill="#9DF310" />
              </BarChart>
            </Grid>
            <Grid item xs={6} sm={3}>
              <Button
                variant="contained"
                disableElevation
                onClick={handleSubmit}
              >
                analyze
              </Button>
            </Grid>
            <Grid item xs={6} sm={3}>
              <Button variant="text" disableElevation>
                How to use
              </Button>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center"
                }}
              >
                {sentiment === "q" && (
                  <DisplaySentiment emoji="ðŸ¤”" sentiment="Question" />
                )}
                {sentiment === "neg" && (
                  <DisplaySentiment emoji="ðŸ™" sentiment="Negative" />
                )}
                {sentiment === "neu" && (
                  <DisplaySentiment emoji="ðŸ˜" sentiment="Neutral" />
                )}
                {sentiment === "pos" && (
                  <DisplaySentiment emoji="ðŸ˜€" sentiment="Positive" />
                )}
              </Box>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
}

export default App;
